var a:string='tststs'
console.log(1)
var div=document.createElement('div')
div.style.border='1px solid red'
div.style.width='100px'
div.style.height='100px'
div.id='demo'
document.body.appendChild(div)

var x=false
var delt=[0,0]
div.onmousedown=(e)=>{
    x=true
    delt=[e.clientX,e.clientY]
}
document.onmousemove=(e)=>{
    if(x==true){
        console.log(e.clientX,e.clientY)
        var newX=e.clientX-delt[0]
        var newY=e.clientY-delt[1]
        var top=parseInt(div.style.top!) || 0//！表示div.style.top不会为空
        var left=parseInt(div.style.left!)  || 0
        div.style.top=top+newY+'px'
        div.style.left=left+newX+'px'
        delt[e.clientX,e.clientY]
        console.log(top)
    }

}
div.onmouseup=()=>{
    x=false
}